.card-section{
  height: 50vh;
  width: 25vw;
  border: 2px solid red;
  border-radius: 5px;
  margin: auto 2vw;
  color: green;
  font-size: 8vh;
  text-align: center;  
}

.img-section{
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  width: 100%;
  height: 50%;
}

p{
  text-decoration: none;
}

/* mobile */
@media screen and (max-width: 800px){
  .card-section{
  width:  50vw;
  font-size: 8vw;  
  }
}